// ProjectList.js
import React, { useEffect, useState } from 'react';
import firebase from 'firebase/app';
import 'firebase/firestore';

function Projects() {
  const [ProjectList, setImages] = useState([]);

  useEffect(() => {
    // Fetch data from Firebase Firestore
    const db = firebase.firestore();

    const userId = firebase.auth().currentUser.uid;

    const imagesRef = db.collection("projects").where("userId", "==", userId);

    imagesRef.onSnapshot((snapshot) => {
      const ProjectList = [];
      snapshot.forEach((doc) => {
        const data = doc.data();
        ProjectList.push({
          id: doc.id,
          ...data,
        });
      });

      setImages(ProjectList);
    });
  }, []);

  return (
    <div>
      <h2>Your Projects</h2>
      <ul>
         {ProjectList.map((image) => (
          <li key={image.id}>
            <strong>{image.imageName}</strong>
            <p>{image.imageDescription}</p>
            <img src={image.imageUrl} alt={image.imageName} />
          </li>
        ))}
      </ul>
    </div>
  );
}

export default Projects;
